.info-rollout-button > .rollout-icon{
  color: rgb(3, 3, 112);
  font-size: 2.5rem;
  animation: changeSize 7s linear infinite, shake 3s linear infinite;
  /* animation: shake 3s infinite; */
  /* animation-timing-function: linear; */
  /* display: none; */
}
@keyframes changeSize {
  10%{
    opacity: 0.9;
  }
  20%{
    opacity: 0.8;
  }
  30%{
    opacity: 0.7;
  }
  40%{
    opacity: 0.7;
  }
  50%{
    opacity: 0.6;
  }
  60%{
    opacity: 0.7;
  }
  70%{
    opacity: 0.7;
  }
  80%{
    opacity: 0.8;
  }
  90%{
    opacity: 0.9;
  }
  100%{
    opacity: 1.0;
  }
}
@keyframes shake {
  0% { transform: translate(0px, 0.5px) rotate(0deg); }
  10% { transform: translate(0px, 1px) rotate(0deg); }
  20% { transform: translate(0px, 1.5px) rotate(0deg); }
  30% { transform: translate(0px, 1px) rotate(0deg); }
  40% { transform: translate(0px, 0.5px) rotate(0deg); }
  50% { transform: translate(0px, 0px) rotate(0deg); }
  60% { transform: translate(0px, -0.5px) rotate(0deg); }
  70% { transform: translate(0px, -1.0px) rotate(0deg); }
  80% { transform: translate(0px, -1.5px) rotate(0deg); }
  90% { transform: translate(0px, -1.0px) rotate(0deg); }
  100% { transform: translate(0px, 0px) rotate(0deg); }
}
